import { configureStore } from '@reduxjs/toolkit';
import filters from '../reducers/filters';
// import heroes from '../reducers/heroes';

// import filters from '../components/heroesFilters/filtersSlice'
import heroes from '../components/heroesList/heroesSlice'
import { connect } from 'react-redux';
import { bindActionCreators } from '@reduxjs/toolkit';
import { getDefaultNormalizer } from '@testing-library/react';

// middleWare -  f       Dispatch 
// (  action    )


//  1 . - store   dispatch & getState 
const stringMiddleware = () => (next) => (action) => {
    if (typeof action === 'string') {
        return next({
            type: action
        })
    }
    return next(action)
}


const enhancer = (createStore) => (...args) => {
    const store = createStore(...args)

    const oldDispatch = store.dispatch  //    dispatch
    store.dispatch = (action) => { //  dispatch 
        if (typeof action === 'number') {
            return oldDispatch({ //   dispatch
                type: action    // c   
            })
        }
        return oldDispatch(action)
    }
    return store;
}


const store = configureStore({
    reducer: {filters, heroes},
    middleware: getDefaultMiddleware => getDefaultMiddleware().concat(stringMiddleware),
    devTools: process.env.NODE_ENV !== 'production',  //  product. - false
})


export default store;

